/* Header
------------------------------------------------------------*/
.c-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1560px;
  margin-left: auto;
  position: relative;
  background: rgba(255, 255, 255, 0.9);
}

.c-header__logo {
  display: flex;
  align-items: center;
  z-index: 99;
}

.c-header__logo--content {
  font-size: 1.8rem;
  font-weight: 600;
  margin-left: 1.15rem;
  line-height: 8rem;
}

.c-header__logo span {
  display: none;
}

.c-header__navsp {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 9%;
  right: -100%;
  transition: ease-in-out .5s;
  z-index: 9;
}

.c-header__navsp.active {
  right: 0;
  transition: ease-in-out .8s;
}

.c-header .is-btn__togglemenu {
  display: none;
}

.c-header .is-btn__togglemenu p {
  font-size: 1.8rem;
}

@media (max-width: 767px) {
  .c-header {
    padding: 20px 40px;
    align-items: inherit;
    border-bottom: 1px dashed #333;
  }
  .c-header__logo span {
    display: block;
  }
  .c-header__logo--content {
    line-height: 22px;
    margin-left: 20px;
  }
  .c-header .is-btn__togglemenu {
    display: block;
  }
  .c-header .is-btn__togglemenu p {
    line-height: 0;
    margin-top: 35px;
    letter-spacing: .1px;
  }
  .c-header__navsp {
    padding: 50px 0 0 0;
  }
  .c-header__navsp--contact {
    text-align: center;
  }
  .c-header__navsp--contact .l-footer2__name {
    font-size: 1.75rem;
  }
  .c-header__navsp .c-nav {
    flex-direction: column;
    align-items: normal;
  }
  .c-header__navsp .c-nav__normal ul {
    flex-direction: column;
  }
  .c-header__navsp .c-nav__normal ul li {
    justify-content: inherit;
    align-items: center;
  }
}

.c-nav {
  display: flex;
  align-items: center;
  z-index: 9;
}

.c-nav__normal > ul, .c-nav__background > ul {
  display: flex;
}

.c-nav__normal > ul > li, .c-nav__background > ul > li {
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 3.2rem;
  position: relative;
}

.c-nav__normal > ul > li {
  margin: 0 2rem;
  letter-spacing: 0.1rem;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.c-nav__normal > ul > li:nth-child(2):hover .c-nav__child {
  display: block;
}

.c-nav__background {
  color: #fff;
}

.c-nav__background--blue {
  background: #48b7d0;
  width: 139px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.c-nav__background--green {
  background: #40a62a;
  width: 139px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.c-nav__background--green .c-nav__child {
  left: -7.3rem;
}

.c-nav__background--green:hover .c-nav__child {
  display: block;
}

.c-nav__child {
  display: none;
  position: absolute;
  top: 100%;
  left: -10.3rem;
  width: 28rem;
  background: rgba(34, 43, 51, 0.7);
  padding: 0 1.25rem;
}

.c-nav__child li {
  padding: 1.15rem 0;
  border-bottom: 1px solid #fff;
  position: relative;
}

.c-nav__child li a {
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 3.2rem;
  color: #fff;
}

.c-nav__child li::after {
  content: "\f054";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  font-size: 1rem;
  color: #fff;
}

.c-nav__child li:last-child {
  border: none;
  padding-bottom: 2rem;
}

.c-nav__child li:last-child::after {
  top: 45%;
}

