/*------------------------------------------------------------
btn
------------------------------------------------------------*/
/*
------------------------------------------------------------*/
.c-btn1 {
  display: inline-block;
  padding: 10px 50px;
  font-size: 1.6rem;
  border: 2px solid #fff;
  border-radius: 20px;
  line-height: 1;
}

.slick-prev,
.slick-next {
  display: none !important;
}

.slick-dots {
  position: absolute;
  top: 52%;
  right: 0;
  transform: translateY(-50%);
  margin-right: 30px;
}

.slick-dots li {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #000;
  margin: 30px 0;
}

.slick-dots li button {
  display: none;
}

.slick-dots li:first-child {
  margin-top: 0;
}

.slick-dots li.slick-active {
  background: #48b7d0;
}

.is-scrollToTop {
  position: fixed;
  z-index: 99;
  opacity: 0;
  animation: scroll 2s infinite;
}

@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-2%);
  }
  100% {
    transform: translateY(0);
  }
}

.is-scrollToTop.active {
  opacity: 1;
  transition: ease-in-out 1s;
}

.is-btn__togglemenu .is-show {
  display: none;
}

.is-btn__togglemenu.active p {
  display: none;
}

.is-btn__togglemenu.active .is-show {
  display: block;
  margin-top: 40px;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(1), .is-btn__togglemenu.active .is-menu__button span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(2), .is-btn__togglemenu.active .is-menu__button span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(1) {
  left: 5px;
  top: 5px;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(2) {
  left: calc( 50% - 5px);
  top: 5px;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(5) {
  left: 5px;
  top: 20px;
}

.is-btn__togglemenu.active .is-menu__button span:nth-child(6) {
  left: calc( 50% - 5px);
  top: 20px;
}

.is-menu__button {
  width: 50px;
  height: 6px;
  position: relative;
  margin: 0;
  outline: none;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}

.is-menu__button span {
  display: block;
  position: absolute;
  height: 6px;
  width: 61%;
  background: #333;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.is-menu__button span:nth-child(even) {
  left: 65%;
  border-radius: 0 3px 3px 0;
}

.is-menu__button span:nth-child(odd) {
  left: 30%;
  border-radius: 3px 0 0 3px;
}

.is-menu__button span:nth-child(1), .is-menu__button span:nth-child(2) {
  top: 0px;
}

.is-menu__button span:nth-child(3), .is-menu__button span:nth-child(4) {
  top: 12px;
}

.is-menu__button span:nth-child(5), .is-menu__button span:nth-child(6) {
  top: 25px;
}

